@font-face {
  font-family: 'gigster';
  src: url('/fonts/gigster.eot?52298485');
  src: url('/fonts/gigster.eot?52298485#iefix') format('embedded-opentype'),
       url('/fonts/gigster.woff?52298485') format('woff'),
       url('/fonts/gigster.ttf?52298485') format('truetype'),
       url('/fonts/gigster.svg?52298485#gigster') format('svg');
  font-weight: normal;
  font-style: normal;
}
